@import url("https://fonts.googleapis.com/css2?family=Noto+Serif:wght@700;800&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Mixed:wght@400&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-noto-serif: "Noto Serif";
  --font-mixed: Mixed;

  /* font sizes */
  --font-size-sm: 14px;
  --font-size-lg: 18px;

  /* border radiuses */
  --br-lg: 18px;
}
